import React from 'react'
import { Alert, Button, Card, Container, Form, FormControl, InputGroup, OverlayTrigger, Tooltip } from 'react-bootstrap'
import { createIframeClient } from "@remixproject/plugin"
import { Celo, NETWORKS } from "@dexfair/celo-web-signer"
import Footer from "./Footer";

function App() {
  const [client, setClient] = React.useState(null)
  const [language, setLanguage] = React.useState('')
  const [fileName, setFileName] = React.useState('')
  const [data, setData] = React.useState([])
  const [constructor, setConstructor] = React.useState({})

  const [account, setAccount] = React.useState('')
  const [network, setNetwork] = React.useState('Mainnet')
  const [selectFileName, setSelectFileName] = React.useState('')
  const [iconSpin, setIconSpin] = React.useState('')
  const [contract, setContract] = React.useState('')
  const [contractAdr0, setContractAdr0] = React.useState('')
  const [error0, setError0] = React.useState('')

  const [celo, setCelo] = React.useState(null)
  const [busy, setBusy] = React.useState(false)

  React.useEffect(() => {
    async function init () {
      if (!client) {
        setClient(createIframeClient())
        const temp = new Celo(network)
        await temp.init(setNetwork, setAccount)
        setCelo(temp)
      } else {
        await client.onload()
        client.solidity.on('compilationFinished', (fn, source, languageVersion, data) => {
          // console.log(fn, source, languageVersion, data)
          setFileName(fn)
          setLanguage(languageVersion)
          setData(data.contracts[fn])
          setContract(Object.keys(data.contracts[fn]).length > 0 ? Object.keys(data.contracts[fn])[0] : '')
          getConstructor(data.contracts[fn][Object.keys(data.contracts[fn])[0]])
        })
        try {
          setSelectFileName(await client.fileManager.getCurrentFile())
        } catch (error) {
          console.error(error)          
        }
        client.on('fileManager', 'currentFileChanged', (fn) => {
          // console.log(fn)
          setSelectFileName(fn)
        })
      }
    }
    init()
  })

  async function connect() {
    if (window.ethereum) {
      window.ethereum.enable();
    }
  }

  async function deploy() {
    if (!busy) {
      setBusy(true)
      const contractData = data[contract.replace(` - ${fileName}`, '')]
      const newContract = new celo.kit.web3.eth.Contract(JSON.parse(JSON.stringify(contractData.abi)))
      const args = []
      if (constructor && constructor.inputs) {
        for(let i = 0; i < constructor.inputs.length; i++) {
          if (constructor.inputs[i].type[constructor.inputs[i].type.length-1] === ']') {
            args.push(JSON.parse(constructor.inputs[i].value))
          } else {
            args.push(constructor.inputs[i].value.toString())
          }
        }
      }
      try {
        const rawTx = {
          from: account,
          data: newContract.deploy({ data: `0x${contractData.evm.bytecode.object}`, arguments: args }).encodeABI()
        }
        const txReceipt = await celo.sendTransaction(rawTx)
        setContractAdr0(txReceipt.contractAddress)
        // console.log(txReceipt.transactionHash)
        setBusy(false)
      } catch (error) {
        // eslint-disable-next-line
        console.error(error)
        setError0(error.toString())
        setBusy(false)
      }
    }
  }

  async function compile() {
    setBusy(true)
    setIconSpin('fa-spin')
    await client.solidity.compile(selectFileName)
    setIconSpin('')
    setBusy(false)
  }

  function atAddress() {
    // TODO: interact with deployed contract
    // client.emit('statusChanged', { key: 'succeed', type: 'success', title: 'Documentation ready !' })
  }

  function getConstructor(contract) {
    setConstructor({})
    for(let i = 0; i < contract.abi.length; i++) {
      if(contract.abi[i].type === 'constructor') {
        const temp = JSON.parse(JSON.stringify(contract.abi[i]))
        for (let i = 0; i < temp.inputs.length; i++) {
          temp.inputs[i].value = ''
          temp.inputs[i].onChange = (e) => {
            temp.inputs[i].value = e.target.value
          }
        }
        setConstructor(temp)
        break
      }
    }
  }

  function MethodParmsForm(props) {
    const list = props.parms.inputs ? props.parms.inputs : []
    const items = list.map((parm, index) => (
      <Form.Group key={index.toString()}>
        <Form.Text className="text-muted">
          <small>{parm.name}</small>
        </Form.Text>
        <Form.Control type="text" placeholder={parm.type} onChange={parm.onChange} size="sm" />
      </Form.Group>))
    return (
      <Form>
        <Contracts contracts={data} fileName={fileName} />
        <div className={list.length === 0 ? 'd-none' : ''}>
          <Card>
            <Card.Header size="sm">
              <small>{ (props.parms.name || props.parms.type) }</small>
            </Card.Header>
            <Card.Body>
              { items }
            </Card.Body>
          </Card>
        </div>
      </Form>
    )
  }

  function handelContract(e) {
    setContract(e.target.value)
    getConstructor(data[e.target.value.replace(` - ${fileName}`, '')])
  }

  function handelNetwork(e) {
    setBusy(true)
    setNetwork(e.target.value)
    celo.changeNetwork(e.target.value)
    setBusy(false)
  }

  function Networks() {
    const list = NETWORKS
    const items = Object.keys(list).map((key) => <option key={key}>{key}</option> )
    return (
      <Form.Group>
        <Form.Text className="text-muted">
          <small>NETWORK</small>
        </Form.Text>
        <Form.Control as="select" value={network} onChange={handelNetwork}>
          {items}
        </Form.Control>    
      </Form.Group>
    )
  }

  function Contracts(props) {
    const list = props.contracts
    const items = Object.keys(list).map((key) => <option key={key}>{`${key} - ${props.fileName}`}</option> )
    return (
      <Form.Group>
        <Form.Text className="text-muted">
          <small>CONTRACT</small>
        </Form.Text>
        <Form.Control as="select" value={contract} onChange={handelContract}>
          {items}
        </Form.Control>
      </Form.Group>
    )
  }

  return (
    <div className="App">
      <Container>
        <Form>
          <Form.Group hidden>
            <Form.Text className="text-muted">
              <small>LANGUAGE</small>
            </Form.Text>
            <Form.Control type="text" placeholder="Language" value={language} size="sm" readOnly />
          </Form.Group>
          <Form.Group hidden>
            <Form.Text className="text-muted">
              <small>FILE NAME</small>
            </Form.Text>
            <Form.Control type="text" placeholder="File Name" value={fileName} size="sm" readOnly />
          </Form.Group>
          <Form.Group>
          <Form.Text className="text-muted">
            <small>ACCOUNT</small>
          </Form.Text>
          <InputGroup>
            <Form.Control type="text" placeholder="Account" value={account} size="sm" readOnly />
            <InputGroup.Append>
              <OverlayTrigger
                placement="left"
                overlay={<Tooltip>Connect to Wallet</Tooltip>}
              >
                <Button className={account !== '' ? 'd-none' : ''} variant="warning" block onClick={connect} size="sm">
                  <small>Connect</small>
                </Button>
              </OverlayTrigger>
            </InputGroup.Append>
          </InputGroup>
          </Form.Group>
          <Networks />
        </Form>
        <hr />
        <Button
          variant="primary"
          onClick={compile}
          block
          disabled={selectFileName===''}
        >
          <i className={`fas fa-sync ${iconSpin}`} style={{marginRight: '0.3em'}} />
          <span>
            Compile&nbsp;{`${selectFileName===''?'<no file selected>':selectFileName.split('/')[selectFileName.split('/').length-1]}`}
          </span>
        </Button>
        <MethodParmsForm parms={constructor} />
        <InputGroup className="mb-3">
          <FormControl value={contractAdr0} size="sm" readOnly />
          <InputGroup.Append>
            <OverlayTrigger
              placement="left"
              overlay={<Tooltip>Open Celo blockscout</Tooltip>}
            >
              <Button
                variant="warning"
                size="sm"
                onClick={() => { window.open(`${NETWORKS[network].blockscout}/address/${contractAdr0}`) }}
                hidden={busy || contractAdr0 === ''}
              >
                <small>Link</small>
              </Button>
            </OverlayTrigger>
            <Button variant="warning" block onClick={deploy} size="sm" disabled={busy || account === '' || data.length === 0}>
              <small>Deploy</small>
            </Button>
          </InputGroup.Append>
        </InputGroup>
        <Alert variant='danger' onClose={() => setError0('')} dismissible hidden={error0===''}>
          <small>{error0}</small>
        </Alert>
      </Container>
      <Footer
        children={<small>by dexfair</small>}
      />
    </div>
  )
}

export default App
